          currentPage: 1
        },
        articleList :[{
          id:"234224",
          title:"",
          author:"zzzzzyyyy",
          createTime:"2020-03-20 22:22:22"
        },
        {
          id:"234234",
          title:"",
          author:"zzzzzyyyy",
          createTime:"2020-03-20 22:22:22"
        }]
      };
    },

    created(){
      // this.getData()
    },
    
    methods:{
      getData(){
        services.getNoticeArticleList(this.query)
        .then((res)=>{
          this.articleList = res.data
        })
        .catch(err=>{
          this.$msgbox({
            type:"errot",
            message:err
          })
        })
      },

       handleCurrentChange() {
        this.getData()
      }
    }
  }
</script>
