<template>
  <article class="article-width article-search">
    <header class="search-wrap">
      <el-input v-model="titleSearch" @change="handlerSearchChange" suffix-icon="el-icon-search"
        placeholder=""></el-input>
    </header>
    <ArticleList :list="articleList" />
    <div class="clearfix" v-show="query.total > 0">
      <el-pagination class="pagenation right" background @current-change="handleCurrentChange"
        :current-page.sync="query.currentPage" :page-size="20" layout="prev, pager, next, jumper" :total="query.total">
      </el-pagination>
    </div>
  </article>
</template>
<script>
  import services from '@/services';
  import ArticleList from '@/components/contents/library/ArticleList';
  export default {
    name: "ArticleSearch",

    components: {
      ArticleList
    },

    data() {
      return {
        query: {
          total: 100,
          currentPage: 1
        },
        titleSearch: "",
        timer: null,
        articleList: [{
            id: "123",
            title: "title",
            author: "",
            createTime: "2020/03/24 12:12:12"
          },
          {
            id: "123",
            title: "title",
            author: "",
            createTime: "2020/03/24 12:12:12"
          }
        ]
      };
    },

    created() {
      // this.getData()
    },

    methods: {
      handlerSearchChange() {
        clearTimeout(this.timer)
        this.timer = setTimeout(() => {
          this.getData()
        }, 300)
      },

      getData() {
        services.getSearchArticleList({
            ...this.query,
            title: this.titleSearch
          })
          .then((res) => {
            this.articleList = res.data
          }, 300)
          .catch(err => {
            this.$msgbox({
              type: "error",
              message: err
            })
          })
      },

      handleCurrentChange() {
        this.getData()
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/scss/variables.scss";

  .article-search{
    padding: 24px 60px;
  }

  .search-wrap {
    margin-bottom: 30px;
    padding: 0 30px;
  }

  .pagenation{
      margin-top: 24px;
      padding: 0 20px;
  }
</style>