import Router from 'vue-router';
import AppMain from '@/components/AppMain.vue';
import Home from '@/components/contents/Home/Index.vue';
import Login from '@/components/contents/Login/Login.vue';
import Register from '@/components/contents/Login/Register.vue';
import ArticleSearch from '@/components/contents/ArticleSearch/Index.vue';
import ArticleDetail from '@/components/contents/library/ArticleDetail.vue';
import notice from '@/components/contents/notice/Index.vue';
import bingchongArticle from '@/components/contents/bingchong/Index.vue';
import zaipeiArticle from '@/components/contents/zaipei/Index.vue';
import ArticlePublish from '@/components/contents/ArticlePublish/Index.vue';
import ArticleManage from '@/components/contents/ArticleManage/Index.vue';
import reedit from '@/components/contents/library/reedit.vue';
import Application from '@/components/contents/Application/Index.vue';
import Mine from '@/components/contents/Mine/Index.vue';
import Vue from 'vue';

Vue.use(Router)

const routes = [{
        path: "/",
        component: AppMain,
        redirect: "/home",
        children: [{
                path: "/home",
                component: Home
            },
            {
                path: '/articleSearch',
                component: ArticleSearch
            },
            {
                path: '/ArticleDetail/:id',
                component: ArticleDetail
            },
            {
                path: '/notice',
                component: notice
            },
            {
                path: '/bingchongArticle',
                component: bingchongArticle
            },
            {
                path: '/zaipeiArticle',
                component: zaipeiArticle
            },
            {
                path: '/ArticlePublish',
                component: ArticlePublish
            },
            {
                path: '/ArticleManage',
                component: ArticleManage
            },
            {
                path: '/ArticleDetail/:linkId',
                props: true,
                component: reedit
            },
            {
                path: '/Application',
                component: Application
            },
            {
                path: '/Mine',
                component: Mine
            },
        ]
    },
    {
        path: "/login",
        component: Login
    },
    {
        path: "/register",
        component: Register
    },
]

const router = new Router({
    routes
})

export default router