<template>
  <div class="wrap article-width">
    <div class="header">
      <label> </label>
      <el-select v-model="query.type">
        <el-option v-for="(option) in articleOptions" :key="option.value" :label="option.label" :value="option.value">
        </el-option>
      </el-select>
    </div>

    <div class="table">
      <el-table :data="tableData">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="title" label="" min-width="200px"></el-table-column>
        <el-table-column prop="author" label="" min-width="80px"></el-table-column>
        <el-table-column prop="updateTime" label="" min-width="100px"></el-table-column>
        <el-table-column label="" min-width="100px">
          <template slot-scope="scope">
            <el-button @click="handleModify(scope.row)" type="text" size="small">
              
            </el-button>
            <el-button @click="handleModify(scope.row)" type="text" size="small">
              
            </el-button>
            <el-button v-if="scope.row.status === 1" @click="handlePublish(scope.row)" type="text" size="small">
              
            </el-button>
            <el-button v-else @click="handleCancelPublish(scope.row)" type="text" size="small">
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
  import {
    mapGetters
  } from 'vuex';
  import services from '@/services';
  export default {
    name: "ArticleManage",

    data() {
      return {
        query: {
          type: null
        },
        tableData: [
          {id: 1, title:"title", author:"author",updateTime:"2020-03-05 15:15:15" ,status:1},
          {id: 2, title:"title", author:"author",updateTime:"2020-03-05 15:15:15" ,status:1},
          {id: 3, title:"title", author:"author",updateTime:"2020-03-05 15:15:15" ,status:0},
          {id: 4, title:"title", author:"author",updateTime:"2020-03-05 15:15:15" ,status:1},
          {id: 5, title:"title", author:"author",updateTime:"2020-03-05 15:15:15" ,status:0},
          {id: 6, title:"title", author:"author",updateTime:"2020-03-05 15:15:15" ,status:1},
          {id: 7, title:"title", author:"author",updateTime:"2020-03-05 15:15:15" ,status:0},
          {id: 8, title:"title", author:"author",updateTime:"2020-03-05 15:15:15" ,status:1},
        ]
      }
    },

    computed: {
      ...mapGetters(['articleOptions'])
    },

    created() {
      this.getData()
    },

    methods: {
      getData() {
        services.getArticleList(this.query)
          .then()
          .catch(err => {
            console.log(err);
          })
      },

      handleDelete(row) {
        services.getArticleList(row.id)
          .then()
          .catch(err => {
            console.log(err);
          })
      },

      handleModify(row) {
        services.deleteArticle(row.id)
          .then()
          .catch(err => {
            console.log(err);
          })
      },

      handlePublish(row) {
        services.getArticleList(row.id)
          .then()
          .catch(err => {
            console.log(err);
          })
      },

      handleCancelPublish(row) {
        services.getArticleList(row.id)
          .then()
          .catch(err => {
            console.log(err);
          })
      },
    }
  }
</script>
<style lang="scss" scoped>
  .wrap {
    padding: 24px 30px;
  }

  .header {
    margin-bottom: 24px;
  }
</style>