const state = {
  activeIndex: localStorage.getItem('activeIndex') || "/home",
  menus: [{
      label: "",
      route: {
        path: "/home"
      }
    },
    {
      label: "",
      route: {
        path: "/articleSearch"
      }
    },
    {
      label: "",
      route: {
        path: "/bingchongArticle"
      }
    },
    {
      label: "",
      route: {
        path: "/zaipeiArticle"
      }
    },
    {
      label: "",
      route: {
        path: "/Application"
      }
    },
    {
      label: "",
      route: {
        path: "/notice"
      }
    },
    {
      label: "",
      route: {
        path: "/ArticlePublish"
      }
    },
    {
      label: "",
      route: {
        path: "/ArticleManage"
      }
    },
    {
      label: "",
      route: {
        path: "/Mine"
      }
    }
  ]
}

const getters = {
  ActiveIndex(state) {
    return state.activeIndex
  },
  menus(state) {
    return state.menus
  },
}

const mutations = {
  setActiveIndex(state, index) {
    state.activeIndex = index
    localStorage.setItem('activeIndex', index)
  }
}


export default {
  state,
  getters,
  mutations
}