<template>
  <div class="record">
    <el-form :model="formData" label-width="80px" ref="form" size='small'>
      <el-form-item prop="name" label="" :rules="formDataRules.name">
        <el-input placeholder="" v-model="formData.name"></el-input>
      </el-form-item>
      <el-form-item prop="phone" label="" :rules="formDataRules.phone">
        <el-input placeholder="" v-model="formData.phone"></el-input>
      </el-form-item>
      <el-form-item prop="address" label="" :rules="formDataRules.address">
        <el-input placeholder="" v-model="formData.address"></el-input>
      </el-form-item>
      <el-form-item prop="amount" label="" :rules="formDataRules.amount">
        <el-input placeholder="" v-model="formData.amount"></el-input>
      </el-form-item>
      <el-form-item prop="reason" label="" :rules="formDataRules.reason">
        <el-input placeholder="" v-model="formData.reason" type="textarea" :autosize="autosize"></el-input>
      </el-form-item>
      <el-form-item prop="fileList" label="" :rules="formDataRules.fileList">
        <el-upload action="https://jsonplaceholder.typicode.com/posts/" :with-credentials="true"
          list-type="picture-card" :file-list="formData.fileList" :on-success='handleSuccess' :on-remove="handleRemove">
          <i class="el-icon-plus"></i>
        </el-upload>
      </el-form-item>
    </el-form>

    <div class="footer">
      <el-button @click="handleReset" size='small'> </el-button>
      <el-button @click="handleSave" size='small' type="primary"> </el-button>
    </div>
  </div>
</template>
<script>
  export default {
    name: "edit",
    data() {
      return {
        formData: {
          name: null,
          phone: null,
          address: null,
          amount: null,
          reason: null,
          fileList: []
        },

        formDataRules: {
          name: [{
            required: true,
            message: "",
            trigger: 'blur'
          }],
          phone: [{
            required: true,
            message: "",
            trigger: 'blur'
          }],
          address: [{
            required: true,
            message: "",
            trigger: 'blur'
          }],
          amount: [{
              required: true,
              message: "",
              trigger: 'blur'
            },
            {
              type: 'number',
              message: "",
              trigger: 'change'
            }
          ],
          reason: [{
            required: true,
            message: "",
            trigger: 'blur'
          }],
        },

        autosize: {
          minRows: 6,
          maxRows: 10
        }
      };
    },

    methods: {
      handleSuccess(res, file, fileList){
        this.formData.fileList = fileList
      },

      handleRemove(file, fileList) {
        this.formData.fileList = fileList
      },

      handleSave() {

      },

      handleReset() {
        this.$refs['form'].resetField()
      },
    }
  }
</script>
<style lang="scss" scoped>
  .record {
    padding: 0 32px;
  }

  .footer {
    margin-top: 30px;
    text-align: center;
    min-width: 74px;
  }
</style>