<template>
  <div class="login">
    <div class="name"></div>
    <div class="title"></div>
    <el-form :model="loginForm" ref="loginForm" label-width="80px">
      <el-form-item label="">
        <el-input placeholder="" class="form-field" v-model="loginForm.username"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input placeholder="" class="form-field" v-model="loginForm.password" type="password"></el-input>
      </el-form-item>
    </el-form>
    <el-button class="btn" type="primary"  @click="handleLogin"></el-button>

    <div class="footer"> <router-link to="/login"></router-link> </div>
  </div>
</template>
<script>
import services from '@/services';
  export default {
    name: "Register",
    data() {
      return {
        loginForm: {
          username: "",
          password: ""
        }
      };
    },

    methods:{
      handleLogin(){
        services.login(this.loginForm)
        .then(res=>{
          if (res.data.code !==200) {
            this.$messgae.error('')
          }else{
            this.$messgae.success('')
            this.$router.push('/home')
          }
        })
        .catch(err=>{
          this.$message.error(err)
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  .login {
    width: 400px;
    margin: 20vh auto 0;
    text-align: center;
  }

  .name{
    font-size: 28px;
    margin-bottom: 50px;
    color:rgba(0, 0, 0, 0.45)
  }

  .title{
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 20px;
  }

  .form-field {
    width: 300px;
  }

  .btn {
    @extend .form-field;
    margin-left: 80px;
  }

  .footer{
    margin-top: 30px;
    padding-top: 20px;
    border-top:1px solid #ccc;
  }
</style>